@import '../../styles/common.less';
@import '../../styles/mixins/combobox.less';
@import '../../styles/mixins/listbox.less';
@import '../../internals/Picker/styles/index.less';

// Cascader
// ----------------------

.bttv-rs-picker-cascader-menu-items > div {
  .clearfix();
}

// Search panel
.bttv-rs-picker-cascader-search-panel {
  max-height: @cascader-search-panel-max-height;
  overflow: auto;
}

.bttv-rs-picker-cascader-row {
  .listbox-option();

  &:hover {
    .listbox-option-active();
  }

  &&-focus {
    .listbox-option-active();
  }

  &:disabled,
  &&-disabled {
    .listbox-options-disabled();

    .bttv-rs-picker-cascader-search-match {
      opacity: 0.7;
      font-weight: normal;
    }
  }
}

.bttv-rs-picker-cascader-col {
  display: inline-block;

  &::after {
    content: '\00a0/\00a0'; //content:' / '
  }

  &:last-child::after {
    display: none;
  }
}

.bttv-rs-picker-cascader-search-match {
  color: var(--bttv-rs-primary-500);
  font-weight: bold;
}

// Column menu
.bttv-rs-picker-cascader-menu-column {
  float: left;
  overflow-y: auto;

  &-loading {
    padding: 4px;
    display: flex;
    align-items: center;
    justify-content: center;

    .bttv-rs-icon {
      margin: 4px;
    }
  }
}

// Menu Items
.bttv-rs-picker-cascader-menu-items {
  ul,
  li {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: @font-size-base;
  }
}

// Menu item
.bttv-rs-picker-cascader-menu-item {
  .listbox-option();

  @padding-right: (16px + @picker-item-content-padding-horizontal);

  position: relative;
  padding: @picker-item-content-padding-vertical @padding-right
    @picker-item-content-padding-vertical @picker-item-content-padding-horizontal;
  word-break: break-word;

  // Has children
  // TODO: move `has-children` class on the .bttv-rs-picker-cascader-menu-item element
  &s-has-children & {
    padding-right: @dropdown-toggle-padding-right;
  }

  &:hover,
  &:focus,
  &.bttv-rs-picker-select-menu-item-focus,
  &&-focus {
    .listbox-option-active();
  }

  &&-active {
    .listbox-option-selected();

    .bttv-rs-picker-cascader-menu-caret {
      color: var(--bttv-rs-text-primary);
    }
  }

  &:disabled,
  &&-disabled {
    .listbox-options-disabled();
  }
}

.bttv-rs-picker-cascader-menu-caret {
  .combobox-indicator-icon();
}
